@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');
*{
    padding: 0;
    margin: 0;
    border: 0;
    outline: 0;
    list-style: none;
    box-sizing: border-box;
    text-decoration: none;
}
body{
    font-family: 'Kumbh Sans', sans-serif;
  
}
img{
    display: block;
    cursor: pointer;
}
a{
    color: #69707D;
}
#root{
    min-height: 100vh;
}